.navbar {
    border: 4px solid white;
    width: 40vw;
    height: 10vh;
    border-radius: 40px;



    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar a {
    color: white;
    text-decoration: none;
}

.navbar a.active {
    color: blue;
}
